<template>
  <div>
  <div id="goods">
    <!-- 商品类型列表 -->
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="menu-item" :class="{'current':currentIndex === index}" @click="selectMenu(index,$event)">
          <span class="text border-1px">
            <!-- 特殊商品类型icon -->
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <!-- 商品展示列表 -->
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click="selectFood(food,$event)" v-for="(food,index) in item.foods" :key="index" class="food-item boder-1px">
              <!-- 商品图片 -->
              <div class="icon">
                <img width="57" height="57" :src="food.icon" alt="">
              </div>
              <!-- 商品信息 -->
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <!-- 商品价格 -->
                <div class="price">
                  <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <!-- 添加,减少商品按钮模块 -->
                <!-- @cart-add 为子组件里设置的监听事件 _drop为当前组件中将会触发的事件 -->
                <div class="cartcontrol-wrapper">
                  <cartcontrol @cart-add="_drop" :food="food"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!-- 底部购物车模块 -->
    <shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
  </div>
  <!-- 商品详情页 -->
  <food :food="selectedFood" ref="food"></food>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import shopcart from 'components/shopcart/shopcart'
import cartcontrol from 'components/cartcontrol/cartcontrol'
import food from 'components/food/food'
const ERR_OK = 0;
export default {
  name: 'goods',
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  },
  data () {
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {}
      }
    },
    computed: {
      // 计算商品类型所在位置的索引
      currentIndex () {
        for (let i = 0; i < this.listHeight.length; i++) {
          let star = this.listHeight[i];
          let end = this.listHeight[i + 1];
          if (!end || (this.scrollY >= star && this.scrollY < end)) {
            return i;
          }
        }
        return 0;
      },
      // 点击商品列表单条商品
      selectFoods () {
        let foods = [];
        this.goods.forEach(good => {
          good.foods.forEach(food => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
  created () {
    // 获取商品数据
    this.$http.get('/api/goods').then(response => {
      response = response.body;
      if (response.errno === ERR_OK) {
        this.goods = response.data;
        this.$nextTick(() => {
          this._initScroll();
          this._calculateHeight();
        });
      }
    });
    // 特殊商品小图标
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  methods: {
    // 将显示内容放到滚动框中
    _initScroll () {
      // 类型列表滚动
      this.menuScorll = new BScroll(this.$refs.menuWrapper, {
        click: true
      });
      // 商品展示列表滚动
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
        click: true,
        probeType: 3
      });
      // 实时监听滚动的位置
      this.foodsScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      })
    },
    // 计算滚动高度
    _calculateHeight () {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    // 购物车小球飞入
    _drop (target) {
      this.$nextTick(() => {
        // 体验优化,异步执行下落动画
        this.$refs.shopcart.drop(target);
      })
    },
    // 点击商品类型按钮 右边商品展示定位到相应位置
    selectMenu (index, event) {
      if (!event._constructed) {
        return;
      }
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let el = foodList[index];
      this.foodsScroll.scrollToElement(el, 300);
    },
    selectFood (food, event) {
      this.selectedFood = food;
      this.$refs.food.show();
    }
  }
}
</script>

<style lang='stylus' scoped>
  @import '../../common/stylus/mixin.styl'
  #goods
    display : flex
    position : absolute
    top : 174px
    bottom : 46px
    width : 100%
    overflow : hidden
    .menu-wrapper
      flex : 0 0 80px
      width : 80px
      .menu-item
        display : table
        padding 0 12px
        width : 54px
        height 56px
        line-height : 14px
        background : #f3f5f7
        &.current
          position : relative
          margin-top : -1px
          z-index : 10
          background  : #ffffff
          font-weight : 700
          .text
            border-none()
        .icon
          vertical-align : top
          display : inline-block
          width : 12px
          height : 12px
          margin-right : 2px
          background-size : 12px 12px
          background-repeat : no-repeat
          &.decrease
              bg-image('decrease_3')
          &.discount
              bg-image('discount_3')
          &.guarantee
              bg-image('guarantee_3')
          &.invoice
              bg-image('invoice_3')
          &.special
              bg-image('special_3')
        .text
          display : table-cell
          width : 56px
          vertical-align : middle
          font-size : 12px
          border-1px (rgba(7, 17, 27, 0.1))
    .foods-wrapper
      flex : 1
      .title
        padding-left : 14px
        height : 26px
        line-height : 26px
        border-left : 2px solid #d9dde1
        font-size : 12px
        color :rgb(147, 153, 159)
        background : #f3f5f7
      .food-item
        display : flex
        margin : 18px
        padding-bottom : 18px
        border-1px(rgba(7, 17, 27, 0.1))
        &:last-child
          border-none()
          margin-bottom : 0
        .icon
          flex : 0 0 57px
          margin-right : 10px
        .content
          flex : 1
          .name
            margin 2px 0 8px 0
            height : 14px
            line-height : 14px
            font-size : 14px
            color : rgb(7, 17, 27)
          .desc, .extra
            line-height : 10px
            font-size : 10px
            color : rgb(147, 153, 159)
          .desc
            margin-bottom : 8px
            line-height : 12px
          .extra
            .count
              margin-right : 12px
          .price
            font-weight : 700
            line-height : 24px
            .now
              margin-right : 8px
              font-size : 14px
              color : rgb(240, 20, 20)
            .old
              text-decoration : line-through
              font-size : 10px
              color : rgb(147, 153, 159)
          .cartcontrol-wrapper
            position : absolute
            right : 0
            bottom : 12px
</style>>



